
@import "../assets/styles/icons/iconfont.css";
#uploadImg {
  max-width: 800px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #f9f9f9;
  border-radius: 20px;
  position: relative;
  text-align: center;
}
#Img {
  opacity: 0;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.imgIcon {
  font-size: 3rem;
}
.boxShow {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.boxItem {
  position: relative;
}
.icon {
  width: 20px;
  height: 20px;
  position: absolute;
}
.icon:before,
.icon:after {
  content: " ";
  position: absolute;
  background-color: #fff;
  display: block;
  width: 100%;
  height: 3px;
  left: 0;
  border-radius: 3px;
  transition: all 0.5s cubic-bezier(0.66, 0.1, 0.38, 1.45);
  top: 50%;
  -webkit-transform: translateY(-51%);
  transform: translateY(-51%);
/* transform-origin: 50% 50%; */
}
.icon:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.icon:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.imgShow {
  width: 200px;
  height: 150px;
}


